* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 0;
  padding: 16px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
  display: grid;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

.header {
  z-index: 100;
  width: 100%;
  background-color: rgba(40, 30, 114, .773);
  justify-content: center;
  padding: 15px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.search-form {
  border: 3px solid #fff;
  border-radius: 26px;
  margin: 0;
  padding: 0 16px;
  transition: all .4s ease-in-out;
  display: flex;
}

.field {
  letter-spacing: 50%;
  color: #f0f8ff;
  background-color: rgba(0, 0, 0, 0);
  border: rgba(0, 0, 0, 0);
  border-radius: 26px 0 0 26px;
  padding: 4px 16px;
  font-size: 20px;
  font-weight: 500;
}

.field:focus {
  outline: none;
}

.field:focus .search-form {
  border-color: #ff0;
}

.field::-ms-input-placeholder {
  color: #aaa;
  font-size: 20px;
  font-weight: 500;
}

.field::placeholder {
  color: #aaa;
  font-size: 20px;
  font-weight: 500;
}

.search-btn {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: rgba(0, 0, 0, 0);
  border-radius: 0 26px 26px 0;
  margin: 0;
  padding: 8px 16px 8px 8px;
}

.search-btn:focus {
  outline: none;
}

.search-btn:disabled .lens {
  fill: #777;
  cursor: none;
}

.search-btn:focus .lens {
  fill: #ff0;
}

.lens {
  fill: #fff;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-bottom: 20px;
  display: flex;
}

.photo-card {
  height: 300px;
  width: 320px;
  border-radius: 8px;
  box-shadow: 8px 8px 8px rgba(48, 47, 47, .1);
}

.info {
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.load-more.is-hidden {
  display: none;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: rgba(40, 30, 114, .773);
  border: rgba(0, 0, 0, 0);
  border-radius: 26px;
  margin-left: auto;
  margin-right: auto;
  padding: 12px 26px;
  font-size: 16px;
  font-weight: 500;
  display: inline-flex;
}

.gallery-item {
  color: inherit;
  font-family: inherit;
  text-decoration: none;
  transition: all .4s ease-in-out;
}

.gallery-item:hover, .gallery-item:focus {
  outline: none;
  scale: 1.05;
}

